<template>
<!-- Loader -->
  <div>
    <div class="accountbg"></div>
  <div class="wrapper-page">
            <div class="card">
                <div class="card-body">

                    <h3 class="text-center m-0">
                        <a href="../../../index.html" class="logo logo-admin"><img src="@/assets/images/DaoYun-Logo.png" width="200"></a>
                    </h3>

                    <div class="p-3">
                        <h4 class="font-18 m-b-5 text-center"></h4>
                        <p class="text-muted text-center"></p>

                        <form class="form-horizontal m-t-30" action="../../../index.html">

                            <div class="form-group">
                                <label for="account"></label>
                                <input type="text" class="form-control" id="account" placeholder="" v-model="account" disabled>
                            </div>

                            <div class="form-group">
                                <label for="oldpassword"></label>
                                <input type="password" class="form-control" id="oldpassword" placeholder="" v-model="oldpassword">
                            </div>

                            <div class="form-group">
                                <label for="newpassword"></label>
                                <input type="password" class="form-control" id="newpassword" placeholder="" v-model="newpassword">
                            </div>

                            <div class="form-group row m-t-20">
                                <div class="col-12 text-right">
                                    <button class="btn btn-primary w-md waves-effect waves-light" type="button"
                    @click="updatecode()"></button>
                                </div>
                            </div>
                        </form>
                    </div>

                </div>
            </div>

            <div class="m-t-40 text-center">
                <p class="text-white"> <router-link to = "/" replace><i class="mdi mdi-lock"></i></router-link> </p>
                <p class="text-white"> 2019-2020.  <i class="mdi mdi-heart text-danger"></i>28.</p>
            </div>

        </div>
    </div>
</template>
<script>

export default {
  name: 'RecoverPassword',
  data () {
    return {
      preloader: true,
      account: localStorage.getItem('account'),
      oldpassword: '',
      newpassword: ''
    }
  },
  created () {
    // 
    setTimeout(this.showPreloader, 2000)
  },
  methods: {
    // 
    showPreloader () {
      this.preloader = false
    },
    updatecode () {
      var t = this
      var myDate = new Date()
      var qs = require('qs')
      this.$axios.post('api/change-pass', qs.stringify({
        ui: localStorage.getItem('userid'),
        ukey: localStorage.getItem('ukey'),
        oldpass: t.oldpassword,
        newpass: t.newpassword,
        TimeStamp: myDate
      }), {
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      })
        .then(function (response) {
          console.log(response.data)
          if (response.data.code === 1) {
            t.restult = ''
          } else if (response.data.code === 9999) {
            t.restult = ''
            t.isShow = true
          } else if (response.data.code === 1001) {
            t.restult = ''
          }
        })
        .catch(function (error) {
          console.log(error)
        })
    }
  }
}
</script>



